<template>
  <div ref="content">
    <div v-if="pageLoading" class="text-center">
      <i class="fa fa-spinner fa-spin fa-2x" />
    </div>
    <div id="messageCard" v-else  class="ml-0">
      <b-card v-for="message in messages" :key="message.message_no">
        <b-card-header>
          <span v-b-toggle="'accordion-' + message.message_no" block>
            <b-row class="dropdownToggleBody d-none d-lg-flex d-md-none">
              <b-col
                cols="12"
                xl="1"
                lg="2"
                class="align-items-center d-flex justify-content-start"
              >
                <template v-if="message.message_sender_icon_path">
                  <div
                    class="thumbnailLogo d-flex justify-content-center align-items-center"
                  >
                    <img
                      v-if="message.message_sender_icon_path != 'null'"
                      v-bind:src="appUrl + message.message_sender_icon_path"
                      width="16%"
                      class="positionImg propostionalLogo"
                    />
                    <img v-else src="@/assets/DefaultIcon.png" width="100%" />
                  </div>
                </template>
                <template v-else>
                  <div class="thumbnailLogo">
                    <img src="@/assets/DefaultIcon.png" width="100%" />
                  </div>
                </template>
              </b-col>
              <b-col
                cols="12"
                lg="8"
                xl="9"
                sm="10"
                class="align-items-center d-flex"
              >
                <b-row class="no-gutters">
                  <b-col cols="3" md="12" xl="12"
                    ><div
                      class="float-left float-lg-none title-block pl-2 senderTxtClass"
                    >
                      {{ message.message_title }}
                      <img
                        v-if="message.attached_file_flg"
                        src="@/assets/file-icon.svg"
                        width="6%"
                        class="ml-2 bacgroundClass"
                      />
                    </div>
                  </b-col>
                  <b-col cols="3" md="12" xl="12">
                    <div
                      class="float-left txt-line-height pl-2 title-block senderTxtClass"
                    >
                      {{ message.message_sender_belongs }} /
                      {{ message.message_sender_person_name }}
                    </div>
                  </b-col>
                </b-row>
              </b-col>
              <b-col
                cols="12"
                xl="2"
                lg="2"
                class="txt-line-height align-items-center d-flex justify-content-end"
              >
                <div
                  class="float-left float-lg-right align-items-center d-flex senderTxtClass2"
                >
                  {{ getDateFormat(message.message_dt) }}
                </div>
              </b-col>
            </b-row>
            <b-row class="dropdownToggleBody d-lg-none d-md-flex">
              <b-col
                cols="1"
                md="1"
                class="align-items-center d-flex justify-content-center"
              >
                <template v-if="message.message_sender_icon_path">
                  <div
                    class="thumbnailLogo d-flex justify-content-center align-items-center"
                  >
                    <img
                      v-if="message.message_sender_icon_path != 'null'"
                      v-bind:src="appUrl + message.message_sender_icon_path"
                      width="16%"
                      class="positionImg propostionalLogo"
                    />
                    <img v-else src="@/assets/DefaultIcon.png" width="100%" />
                  </div>
                </template>
                <template v-else>
                  <div class="thumbnailLogo">
                    <img src="@/assets/DefaultIcon.png" width="100%" />
                  </div>
                </template>
              </b-col>
              <b-col cols="8" md="8" class="ml-1">
                <div>
                  <b-row>
                    <b-col class="senderTxtClass">
                      {{ message.message_title }}
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col class="senderTxtClass">
                      {{ message.message_sender_belongs }} /
                      {{ message.message_sender_person_name }}
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col class="senderTxtClass">
                      {{ getDateFormat(message.message_dt) }}
                    </b-col>
                  </b-row>
                </div>
              </b-col>
              <b-col
                cols="2"
                md="2"
                class="float-right align-items-center d-flex justify-content-end"
              >
                <img
                  v-if="message.attached_file_flg"
                  src="@/assets/file-icon.svg"
                  width="300%"
                  class="bacgroundClass2"
                />
              </b-col>
            </b-row>
          </span>
        </b-card-header>

        <b-collapse
          :id="'accordion-' + message.message_no"
          :visible="message.message_no == activeMessageNo"
          accordion="my-accordion"
          role="tabpanel"
        >
          <b-card-body class="mt-3">
            <div>
              <b-row>
                <b-col>
                  <b-card-text
                    v-html="message.message_text ? message.message_text : ''"                   
                    class="msgClass"
                  ></b-card-text>
                  <b-link
                    @click="navigateToProposal"
                    v-if="
                      message.message_destination_account_type == '001' &&
                      message.message_type == '002'
                    "
                    class="navigationFont"
                  >
                    <u>{{ $t("TOREDUCTIONPROPOSAL") }}</u>
                  </b-link>

                  <b-card v-if="message.attached_file_flg" class="mt-3">
                    <div>
                      <b-row class="d-flex">
                        <b-col
                          v-for="(item, index) in message.attached_file_data"
                          v-bind:key="index"
                          cols="10"
                          xl="5"
                          lg="5"
                          class="mb-2 mr-2 ml-4 imgbacgroundClass"
                        >
                          <img
                            v-if="item.extension == 'xlsx'"
                            src="@/assets/exl.png"
                            width="3%"
                          />
                          <img
                            v-else-if="item.extension == 'pdf'"
                            src="@/assets/pdf.png"
                            width="3%"
                          />
                          <img
                            v-else-if="item.extension == 'ppt'"
                            src="@/assets/ppt.png"
                            width="3%"
                          />
                          <img
                            v-else-if="item.extension == 'doc'"
                            src="@/assets/word.png"
                            width="3%"
                          />
                          <img
                            v-else-if="item.extension == 'zip'"
                            src="@/assets/zip.png"
                            width="3%"
                          />
                          <img v-else src="@/assets/other.png" width="3%" />

                          <a
                            :href="appUrl + item.attached_file_path"
                            v-text="item.attached_file_name"                          
                            class="ml-1"
                            download
                          />
                        </b-col>
                      </b-row>
                    </div>
                  </b-card>
                </b-col>
              </b-row>
              <b-row>
                <b-col class="d-flex justify-content-center mt-4">
                  <b-button
                    @click="showMessageModal"
                    class="text-center mobile-btn mt-3"
                    variant="primary"
                    >{{ $t("REPLY") }}
                  </b-button>
                </b-col>
              </b-row>
            </div>
          </b-card-body>
        </b-collapse>
      </b-card>
    </div>
    <br />

    <b-modal
      id="message-modal"
      ref="message-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0 "
      body-class="modal-padding"
      size="lg"
      footer-class="border-0 "
      header-class="border-0 py-0 bg-white"
      button-size="sm"
    >
      <template v-slot:modal-header="{ cancel }">
        <b-row></b-row>
        <div class="">
          <button
            @click="cancel"
            type="button"
            aria-label="Close"
            class="close close-btn"
          >
            ×
          </button>
        </div>
      </template>
      <b-form @submit.prevent="submit" enctype="multipart/form-data">
        <b-input-group class="mb-4 mt-1">
          <b-input
            v-model.trim="$v.proposal.subject.$model"
            type="text"
            class="headerClass"
          />
        </b-input-group>
        <show-errors :control="$v.proposal.subject" :field="$t('SUBJECT')" />
        <ckeditor
          :editor="editor"
          v-model.trim="$v.proposal.mailContent.$model"
          :config="editorConfig"
        ></ckeditor>
        <show-errors
          :control="$v.proposal.mailContent"
          :field="$t('MAILCONTENT')"
        />
        <b-row class="mt-4">
          <b-col cols="12" xl="12">
            <div v-if="files.length">
              <div
                id="upload-attach"
                v-for="(file, index) in files"
                v-bind:key="index"
              >
                <img src="@/assets/file-icon.svg" width="2%" />
                {{ file.name }}
                <b-link
                  @click="deleteFile(index)"
                  href="javascript:;"
                  class="card-link link"
                >
                  <i class="fa fa-times text-danger"></i>
                </b-link>
              </div>
            </div>
            <b-form-file
              id="avatar"
              ref="file"            
              @change="onFileChange"
              :placeholder="$t('ATTACHFILE')"
              drop-placeholder="$t('ATTACHFILE')"
              data-iconName="fa fa-upload"
              multiple="multiple"
            >
              <template slot="file-name" slot-scope="{}">
                {{ $t("ATTACHFILE") }}
              </template></b-form-file
            >
            <p class="text-danger">{{ fileErrorMessage }}</p>
          </b-col>
        </b-row>
        <b-row>
          <b-col cols="12">
            <div
              class="d-none d-md-none d-lg-flex justify-content-center pr-2 date-fs line-4 text-center textFontCls"
            >
              {{ $t("REPLAYPOPUPMESSAGE") }}
            </div>
            <div
              class="d-sm-flex d-lg-none pr-2 date-fs line-4 text-center textFontCls"
            >
              <b-col cols="12">{{ $t("REPLAYPOPUPMESSAGE1") }}</b-col>
              <b-col cols="12">{{ $t("REPLAYPOPUPMESSAGE2") }}</b-col>
            </div>
          </b-col>
          <b-col cols="12"
            ><b-form-checkbox v-model="status" size="sm" class="text-center"
              ><div class="small-fs textFontCls">
                {{ $t("CONSENT") }}
              </div></b-form-checkbox
            >
          </b-col>
          <b-col cols="12" class="text-center mt-1">
            <b-button
              :disabled="!status || disabled"
              type="submit"
              variant="primary"
              class="mobile-btn mt-1"
              >{{ $t("SEND") }}
              <i v-if="loading" class="fa fa-spinner fa-spin" />
            </b-button>
          </b-col>
        </b-row>
      </b-form>
    </b-modal>
    <b-modal
      id="reply-complete-modal"
      ref="reply-complete-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="lg"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{ cancel }">
        <b-row></b-row>
        <div class="">
          <button
            @click="cancel"
            type="button"
            aria-label="Close"
            class="close close-btn"
          >
            ×
          </button>
        </div>
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col class="replyPopup">
            <div class="title-fw title-fs text-left">
              {{ $t("REPLYSENT1") }}
              <span class="link-color">{{ serviceName }}</span>
              {{ $t("REPLYSENT2") }}
            </div>
            <div class="title-fw title-fs text-left">
              {{ $t("PLEASEWAIT") }}
            </div>
          </b-col>
        </b-row>
      </b-card>
    </b-modal>
    <b-row class="mt-5">
      <b-col
        cols="12"
        class="d-flex align-items-center justify-content-end mblreturnBtn justifyMobile"
      >
        <b-button
          @click="returnToReadList"
          variant="dark"
          class="d-flex date-fs align-items-center justify-content-center returnBtn"
        >
          <i class="fa fa-caret-left fa-2x mr-2" aria-hidden="true"></i
          >{{ $t("LISTOFNOTICES") }}</b-button
        >
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import { Settings } from "luxon";
import { mapGetters } from "vuex";
import { Config } from "@/shared/config";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
Settings.defaultLocale = "ja";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { endPoint } from "@/shared/endPoint";

export default {
  name: "ProposalMessage",
  components: { ShowErrors },
  data() {
    return {
      appUrl: Config.apiBaseUrl,
      baseDOMAIN: Config.apiDomain,
      default_icon: "fa-user",
      proposal: {
        subject: "",
        mailContent: "",
      },
      files: [],
      formDataFormat: [],
      replySubject: "",
      currentFile: "",
      uploadedFiles: [],
      serviceName: "",
      messages: [],
      status: false,
      disabled: false,
      fileErrorMessage: "",
      loading: false,
      pageLoading: false,
      editor: ClassicEditor,
      editorConfig: {
        language: Settings.defaultLocale,
        toolbar: {
          items: [
            "heading",
            "|",
            "bold",
            "italic",
            "bulletedList",
            "numberedList",
            "|",
            "indent",
            "outdent",
            "|",
            "blockQuote",
            "insertTable",
            "undo",
            "redo",
          ],
        },
        table: {
          contentToolbar: ["tableColumn", "tableRow", "mergeTableCells"],
        },
        activeMessageNo: "",
        reductionMessageNo: "",
      },
      expenseDetails: {
        accounts_name_cd: "",
        expense_item_cd: "",
        expense_item_nm: "",
      },
    };
  },
  validations: {
    proposal: {
      subject: { required },
      mailContent: { required },
    },
  },
  computed: {
    ...mapGetters({
      proposal_info: "getProposalInfo",
      client_details: "getUserDetails",
    }),
  },
  mounted() {
    this.activeMessageNo = this.proposal_info.message_no;
    this.reductionMessageNo = this.proposal_info.message_no;
  },
  created() {
    this.getConversation();
    this.getExpenseDetails();
  },
  methods: {
    returnToReadList() {
      this.$router.push({ name: "user_reduction_proposal" });
    },
    /**Function to convert the date based on required format for UI */
    getDateFormat(date) {
      if (!date) {
        return "";
      }
      return (
        date.slice(0, 4) + "/" + date.slice(5, 7) + "/" + date.slice(8, 10)
      );
    },
    /**Function to display reply modal */
    showMessageModal() {
      this.fileErrorMessage = "";
      this.$v.$reset();
      this.uploadedFiles = [];
      this.files = [];
      this.proposal.mailContent = "";
      this.status = false;
      let title = this.messages[this.messages.length - 1].message_title
        ? this.messages[this.messages.length - 1].message_title
        : "";
      let sub_title = title;
      sub_title = sub_title.substring(0, 3);
      if (sub_title.toUpperCase() != "re:".toUpperCase()) {
        title = "re:" + title;
      }
      this.proposal.subject = title;
      this.$refs["message-modal"].show();
    },
    hideMessageModal() {
      this.$refs["message-modal"].hide();
    },
    hideReplyModal() {
      this.$refs["reply-complete-modal"].hide();
    },
    /**Function to get the details about the expenseItem data based on selected expenseItem proposal message */
    async getExpenseDetails() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.READWITHJOINMEXPENSEITEM,
        {
          paginate: false,
          fieldlist: [
            "t_reduction_proposal.client_no",
            "m_accounts_name.accounts_name_cd",
            "m_accounts_name.accounts_name_nm",
            "m_expense_item.expense_item_cd",
            "m_expense_item.expense_item_nm",
          ],
          joins: [
            {
              table: "t_reduction_proposal",
              join_type: "inner",
              join_conditions: [
                {
                  join_using: "t_reduction_proposal.expense_item_cd",
                  join_operator: "=",
                  join_with: "m_expense_item.expense_item_cd",
                },
                {
                  join_using: "t_reduction_proposal.accounts_name_cd",
                  join_operator: "=",
                  join_with: "m_expense_item.accounts_name_cd",
                },
              ],
            },
            {
              table: "m_accounts_name",
              join_type: "inner",
              join_conditions: [
                {
                  join_using: "m_accounts_name.accounts_name_cd",
                  join_operator: "=",
                  join_with: "t_reduction_proposal.accounts_name_cd",
                },
              ],
            },
          ],
          conditions: [
            {
              key: "t_reduction_proposal.reduction_proposal_no",
              value: this.proposal_info.proposal_no,
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data.data;
        if (responseData.length > 0) {
          this.expenseDetails = responseData[0];
        }
      }
    },
    /**Function to reply for selected expenseItem Proposal message alias conversation reply */
    async postDataToReplyAPI() {
      this.loading = true;
      this.disabled = true;

      /** To get receiver message no */
      const receiver_filtered_data = this._.find(this.messages, (item) => {
        //ToDO destination type hardcode need to reCheck
        return item.message_sender_account_type != "001" && item.message_type == "002";
      });

      const { data } = await BasicInfoApis.postAPI(endPoint.PROPOSALMESSAGE, {
        message_no: receiver_filtered_data.message_no,
      });
      this.loading = false;
      this.disabled = false;
      if (data.success) {
        this.reply(data);
      }
    },
    /**Function to compile the data for sending to API for mail processing */
    async reply(receiverData) {
      let response = receiverData.data;
      this.serviceName = response.message_sender_belongs;

      var formData = new FormData();
      for (let i = 0; i < this.files.length; i++) {
        formData.append("attachments[]", this.files[i]);
      }
      formData.append("message_type", response.message_type);
      formData.append(
        "message_sender_account_type",
        response.message_destination_account_type
      );
      formData.append(
        "message_sender_account_no",
        response.message_destination_account_no
      );
      formData.append(
        "message_sender_person_name",
        response.message_destination_person_name
      );
      formData.append(
        "message_sender_belongs",
        this.client_details.clientCompanyName
      );
      formData.append(
        "message_destination_account_type",
        response.message_sender_account_type
      );
      formData.append(
        "message_destination_account_no",
        response.message_sender_account_no
      );
      formData.append(
        "message_destination_person_name",
        response.message_sender_person_name
      );

      formData.append("message_title", this.proposal.subject);
      formData.append("message_text", this.proposal.mailContent);
      formData.append("reduction_proposal_no", this.proposal_info.proposal_no);
      formData.append(
        "reduction_proposal_url",
        response.reduction_proposal_url
      );
      formData.append(
        "message_sender_icon_path",
        this.client_details.icon_path
      );
      formData.append(
        "attached_file_flg",
        this.files.length > 0 ? true : false
      );

      formData.append("create_id", this.client_details.client_account_no);
      formData.append("update_id", this.client_details.client_account_no);
      formData.append("client_no", this.client_details.client_account_no);
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MESSAGEREPLY,
        formData
      );

      if (data.success) {
        this.hideMessageModal();
        this.getConversation();
        this.$refs["reply-complete-modal"].show();
        this.activeMessageNo = data.data.message_no ? data.data.message_no : "";
      }
    },
    /**Function for submit the replied subject and context validation check */
    async submit() {
      this.$v.proposal.$touch();
      if (!this.$v.proposal.$invalid) {
        this.postDataToReplyAPI();
      }
    },
    /**Function to diplay the conversation for particular expenseItem proposal message in format of accordion */
    async getConversation() {
      this.loading = true;
      this.pageLoading = true;
      this.disabled = true;

      const { data } = await BasicInfoApis.postAPI(
        endPoint.MESSAGECONVERSATION,
        {
          request_proposal_no: this.proposal_info.proposal_no,
          message_type: this.proposal_info.message_type,
        }
      );
      this.loading = false;
      this.pageLoading = false;
      this.disabled = false;

      if (data.success) {
        this.messages = data.data;
        this.messages.forEach((item) => {
          if (item.attached_file_data.length > 0) {
            for (var i = 0; i < item.attached_file_data.length; i++) {
              var ext = item.attached_file_data[i].attached_file_name
                .split(".")
                .pop();

              Object.assign(item.attached_file_data[i], { extension: ext });

              // item.attached_file_data.assign(ext)
              console.log(item, "item.attached_file_data");
            }
          }
        });
      } else {
        this.fileErrorMessage = this.$t("backend." + data.message.code);
      }
    },
    /**Function to load the browsed file and checking validation and uploading part of the file */
    onFileChange(event) {
      this.fileErrorMessage = "";

      let fileLength = this.files.length;
      if (event.target.files.length + fileLength > 5) {
        this.fileErrorMessage = this.$t("MAXFILENO");
        return true;
      }
      let fileSize = 0;
      for (var i = 0; i < event.target.files.length; i++) {
        var ext = event.target.files[i].name.split(".").pop();
        if (ext == "sh" || ext == "exe") {
          this.fileErrorMessage = this.$t("CANTNOTUPLOADEXCECUTABLEFILE");
          return true;
        }
        fileSize += Math.round(event.target.files[i].size / 1024);
      }
      if (fileSize > 10240) {
        this.fileErrorMessage = this.$t("MAXFILESIZE");
        return true;
      }

      for (let i = 0; i < event.target.files.length; i++) {
        this.files.push(event.target.files[i]);
      }
      event.target.value = "";
    },
    /**Function to delete the browsed file from UI */
    deleteFile(index) {
      this.files.splice(index, 1);
    },
    /**Function to navigate from conversation screen to [proposalListScreen] for particular expenseItem */
    async navigateToProposal() {
      this.loading = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MESSAGEREADREDUCTIONPROPOSAL,
        {
          reduction_proposal_no: this.proposal_info.proposal_no,
        }
      );
      this.loading = false;
      if (data.success) {
        this.$router.push({ name: "reduction_proposal_preview" });

        this.$store.dispatch("updateSelectedExpenseItem", {
          accounts_name_cd: this.expenseDetails.accounts_name_cd
            ? this.expenseDetails.accounts_name_cd
            : "",
          expense_item_cd: this.expenseDetails.expense_item_cd
            ? this.expenseDetails.expense_item_cd
            : "",
          expense_item_nm: this.expenseDetails.expense_item_nm
            ? this.expenseDetails.expense_item_nm
            : "",
        });
      } else {
        this.fileErrorMessage = this.$t("backend." + data.message.code);
      }
    },
  },
};
</script>

<style scoped lang="scss">
$primary: #062854 !default;
$accordian_background: #ffff;
$accordian_background_hover: #ffff;

.positionImg {
  position: absolute !important;
}
.textFontCls {
